body{

    overflow: none;
}

.TeamCover {
    position: fixed;
    background-image: url('../../public/team_cover.jpg');
    background-size: cover;
    background-position: 0 20%;
    height: 60vh;
    width: 100%;
}

.TeamContainer {
    position: fixed;
    top: 15vh;
    width: 100%;
    height: 100vh;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 960px){
    .TeamCover {
        background-position: 50% 20%;
    }
    .TeamContainer{
        top: 5vh;
    }
}