.MemberContainer {
    padding: 30px;
    height: 60vh;
    width: 40vh;
    background-color: rgba(255, 255, 255);
    box-shadow: 0px 10px 10px rgba(0,0,0, 0.2);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.ProfilePicture {
    min-height: 20vh;
    max-height: 20vh;
    width: 20vh;
    border-radius: 100%;
    background-image: url('./mock.png');
    background-size: cover;
    background-repeat: no-repeat;
}

.MemberTextContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 100%;
    width: 100%;
}

h1 {
    padding: 20px 0;
}

p {
    width: 100%;
    font-size: 0.8rem;
}

.ContactIcons {
    font-size: 1.8rem;
    padding-top: 40px;
}

.ContactIcons i {
    padding: 0 20px;
}